.header {
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.logo {
  justify-content: center;
  align-items: center;
  display: flex;
}

@media (width <= 820px) {
  .logo {
    width: 100%;
    padding-top: 10px;
  }
}

.nav__list {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:first-of-type) {
  margin-left: 20px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.stars {
  gap: 8px;
  padding-left: 2px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 12px;
  height: 12px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.cards {
  grid-template-columns: 200px;
  justify-content: center;
  gap: 46px 48px;
  margin-bottom: 72px;
  padding: 50px 40px 0;
  display: grid;
}

@media (width >= 488px) {
  .cards {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards {
    grid-template-columns: repeat(4, 200px);
  }
}

.product-card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 0;
  transition: all .3s ease-in;
}

.product-card:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.product-card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card:hover > .product-card__title {
  color: #34568b;
}

.product-card__code {
  color: #616070;
  margin-bottom: 18px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__rate {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.product-card__reviews {
  color: #060b35;
  text-align: right;
  font-size: 10px;
  line-height: 14px;
}

.product-card__cost {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product-card__price {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.product-card__amount {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__btn {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-bottom: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.product-card__btn:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

html {
  font-family: Roboto, sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
}

